import { createStore } from 'redux';
import icons from './manifest';

const START_LOADING = 'START_LOADING';
const CHOOSE_PERSONE = 'CHOOSE_PERSONE';
const CLOSE_WINDOW = 'CLOSE_WINDOW';

const initialState = {
  icons_heros: null,
  selectedPersonId: 0,
  src: "",
  isOpen: false,
};

export const startLoading = () => ({ type: START_LOADING });
export const choosePersone = (event, img) => ({ type: CHOOSE_PERSONE, event, img });
export const handleClose = () => ({ type: CLOSE_WINDOW })

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case START_LOADING:
      return {
        ...state,
        icons_heros: icons,
      }
    case CLOSE_WINDOW:
      return {
        ...state,
        isOpen: false,
      }  

    case CHOOSE_PERSONE:
        if (action.event === 13) {
            return {
              ...state,
              isOpen: true,
            }
         
        }
       if (action.event === 37) {
        if (state.selectedPersonId === 14) {
          return {
            ...state,
            selectedPersonId: 12,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 20) {
          return {
            ...state,
            selectedPersonId: 18,
            src: action.img,
          }
        }
        return {
          ...state,
          selectedPersonId: state.selectedPersonId > 0 ? state.selectedPersonId - 1 : 24,
        }
      }
      if (action.event === 38) {
        if (state.selectedPersonId === 0) {
          return {
            ...state,
            selectedPersonId: 7,
            src: action.img,
          }
        }
       
        if (state.selectedPersonId === 1) {
          return {
            ...state,
            selectedPersonId: 20,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 2) {
          return {
            ...state,
            selectedPersonId: 21,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 3) {
          return {
            ...state,
            selectedPersonId: 22,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 4) {
          return {
            ...state,
            selectedPersonId: 23,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 5) {
          return {
            ...state,
            selectedPersonId: 24,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 6) {
          return {
            ...state,
            selectedPersonId: 12,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 16) {
          return {
            ...state,
            selectedPersonId: 3,
            src: action.img,
          }
        }
      
       
        if (state.selectedPersonId <= 9 || state.selectedPersonId === 18 || state.selectedPersonId === 17) {
          return {
            ...state,
            selectedPersonId: state.selectedPersonId - 7,
            src: action.img,
          }
        }
        
        return {
          ...state,
          selectedPersonId: state.selectedPersonId - 6,
          src: action.img,
        }
      }
      if (action.event === 40) {
        if (state.selectedPersonId === 3) {
          return {
            ...state,
            selectedPersonId: 16,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 7) {
          return {
            ...state,
            selectedPersonId: 0,
            src: action.img,
          }
        }
       
        if (state.selectedPersonId === 20) {
          return {
            ...state,
            selectedPersonId: 1,
            src: action.img,
          }
        }
      
        if (state.selectedPersonId === 21) {
          return {
            ...state,
            selectedPersonId: 2,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 22) {
          return {
            ...state,
            selectedPersonId: 3,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 23) {
          return {
            ...state,
            selectedPersonId: 4,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 24) {
          return {
            ...state,
            selectedPersonId: 5,
            src: action.img,
          }
        }
       
        if (state.selectedPersonId >= 4 && state.selectedPersonId !== 10 && state.selectedPersonId !== 11) {
          return {
            ...state,
            selectedPersonId: state.selectedPersonId + 6,
            src: action.img,
          }
        }
        return {
          ...state,
          selectedPersonId: state.selectedPersonId + 7,
          src: action.img,
        }
      }
      if (action.event === 39) {
        if (state.selectedPersonId === 12) {
          return {
            ...state,
            selectedPersonId: 14,
            src: action.img,
          }
        }
        if (state.selectedPersonId === 18) {
          return {
            ...state,
            selectedPersonId: 20,
            src: action.img,
          }
        }
        return {
          ...state,
          selectedPersonId: state.selectedPersonId < 24 ? state.selectedPersonId + 1 : 0 ,
          src: action.img,
        }
      } 

      default:
        return state;
  }
};

const store = createStore(
 reducer,
);

export default store;